import { createBlock } from "./blocks";
import { Board } from "./board";

/* global Excel, OfficeExtension */

// ExcelåŠ è½½é¡¹æ¸¸æˆä¸»é€»è¾‘
let board;
let currentBlock;
let gameInterval;
let keyDownHandler;
let score = 0;
let isGameRunning = false;

// é”™è¯¯å¤„ç†å‡½æ•°
function handleExcelError(operation, error) {
    console.error(`${operation} å¤±è´¥:`, error);
    if (error instanceof OfficeExtension.Error) {
        console.error('Office Extension é”™è¯¯:', error.debugInfo);
    }
    stopGame();
}

// ç¡®ä¿å·¥ä½œè¡¨å­˜åœ¨
async function ensureWorksheetExists(context, sheetName) {
    try {
        const sheet = context.workbook.worksheets.getItem(sheetName);
        sheet.load("name");
        await context.sync();
        return sheet;
    } catch (error) {
        // å¦‚æœå·¥ä½œè¡¨ä¸å­˜åœ¨ï¼Œåˆ›å»ºæ–°å·¥ä½œè¡¨
        const newSheet = context.workbook.worksheets.add(sheetName);
        await context.sync();
        return newSheet;
    }
}

export function playGame() {
    // å¦‚æœæ¸¸æˆæ­£åœ¨è¿è¡Œï¼Œå…ˆåœæ­¢å½“å‰æ¸¸æˆ
    stopGame();

    Excel.run(async (context) => {
        try {
            // ç¡®ä¿å·¥ä½œè¡¨å­˜åœ¨
            let workSheet;
            try {
                workSheet = context.workbook.worksheets.getItem("Sheet1");
            } catch (error) {
                workSheet = context.workbook.worksheets.add("Sheet1");
            }

            const gameRange = workSheet.getRange("A1:L22");
            gameRange.format.fill.color = "black";
            gameRange.format.columnWidth = 15;
            await context.sync();
            const playRange = workSheet.getRange("B2:K21");
            playRange.format.fill.color = "white";
            await context.sync();
            board = new Board(10, 20);

            startGame();
        } catch (error) {
            handleExcelError("åˆå§‹åŒ–æ¸¸æˆ", error);
        }
    }).catch(error => {
        handleExcelError("Excelæ“ä½œ", error);
    });
}

function startGame() {
    // æ¸…ç†ä¹‹å‰çš„æ¸¸æˆçŠ¶æ€
    stopGame();
    
    isGameRunning = true;
    score = 0;
    currentBlock = createBlock(board);
    
    // åˆ›å»ºå‘½åçš„äº‹ä»¶å¤„ç†å‡½æ•°ï¼Œä»¥ä¾¿åç»­ç§»é™¤
    keyDownHandler = (event) => {
        if (!isGameRunning) return;
        
        if (event.code === "ArrowUp") {
            blockMove("rotate");
        } else if (event.code === "ArrowLeft") {
            blockMove("left");
        } else if (event.code === "ArrowRight") {
            blockMove("right");
        } else if (event.code === "ArrowDown") {
            blockMove("down");
        }
    };
    
    document.addEventListener("keydown", keyDownHandler);
    
    if (gameInterval) clearInterval(gameInterval);
    gameInterval = setInterval(() => {
        if (!isGameRunning || !currentBlock) {
            stopGame();
            return;
        }
        
        if (!currentBlock.down()) {
            board.placeBlock(currentBlock);
            const linesCleared = board.clearLines();
            score += linesCleared * 100;
            currentBlock = createBlock(board);

            // æ£€æŸ¥æ¸¸æˆç»“æŸ
            if (!board.isValidPosition(currentBlock.space)) {
                stopGame();
                console.log(`æ¸¸æˆç»“æŸï¼å¾—åˆ†: ${score}`);
            }
        }
        renderBoard();
    }, 500);
    
    renderBoard();
}

function stopGame() {
    isGameRunning = false;
    
    if (gameInterval) {
        clearInterval(gameInterval);
        gameInterval = null;
    }
    
    if (keyDownHandler) {
        document.removeEventListener("keydown", keyDownHandler);
        keyDownHandler = null;
    }
}

function handleKeyDown(event) {
    if (event.code === "ArrowUp")
        blockMove("rotate");
    if (event.code === "ArrowLeft")
        blockMove("left");
    if (event.code === "ArrowRight")
        blockMove("right");
    if (event.code === "ArrowDown")
        blockMove("down");
}

function blockMove(direction) {
    if (!board || !currentBlock || !isGameRunning) return;
    
    let moveSuccess = false;
    
    switch(direction) {
        case "rotate":
            moveSuccess = currentBlock.rotate();
            break;
        case "left":
            moveSuccess = currentBlock.left();
            break;
        case "right":
            moveSuccess = currentBlock.right();
            break;
        case "down":
            moveSuccess = currentBlock.down();
            break;
    }
    
    // åªæœ‰ç§»åŠ¨æˆåŠŸæ—¶æ‰æ¸²æŸ“
    if (moveSuccess) {
        renderBoard();
    }
}

function renderBoard() {
    if (!board || !currentBlock || !isGameRunning) return;

    Excel.run(async (context) => {
        try {
            const workSheet = context.workbook.worksheets.getItem("Sheet1");
            const playRange = workSheet.getRange("B2:K21");

            // æ¸…é™¤æ•´ä¸ªæ¸¸æˆåŒºåŸŸ
            playRange.format.fill.color = "white";

            // ç»˜åˆ¶å›ºå®šæ–¹å—
            for (let y = 0; y < board.height; y++) {
                for (let x = 0; x < board.width; x++) {
                    if (board.grid[y][x]) {
                        playRange.getCell(y, x).format.fill.color = board.grid[y][x];
                    }
                }
            }

            // ç»˜åˆ¶å½“å‰æ´»åŠ¨æ–¹å—
            currentBlock.space.forEach(pos => {
                if (pos.y >= 0 && pos.y < board.height && pos.x >= 0 && pos.x < board.width) {
                    playRange.getCell(pos.y, pos.x).format.fill.color = currentBlock.color;
                }
            });

            // æ›´æ–°åˆ†æ•°
            const scoreCell = workSheet.getRange("M2");
            scoreCell.values = [[`å¾—åˆ†: ${score}`]];

            await context.sync();
        } catch (error) {
            console.error("æ¸²æŸ“æ¸¸æˆæ¿å¤±è´¥:", error);
            if (error instanceof OfficeExtension.Error) {
                console.error('Office Extension é”™è¯¯:', error.debugInfo);
            }
            // æ¸²æŸ“å¤±è´¥æ—¶åœæ­¢æ¸¸æˆ
            stopGame();
        }
    }).catch(error => {
        console.error("Excelæ¸²æŸ“æ“ä½œå¤±è´¥:", error);
        stopGame();
    });
}

function gameOver() {
    Excel.run(async (context) => {
        try {
            const message = "GAME OVER!";
            const range = context.workbook.worksheets.getItem("Sheet1").getRange("B2:K3");
            range.format.fill.color = "black";
            range.format.font.color = "red";
            for (let i = 0; i < message.length; i++) {
                range.getCell(0, i).values = message[i];
            }
            await context.sync();
        } catch (error) {
            console.error("æ¸¸æˆç»“æŸæ˜¾ç¤ºå¤±è´¥:", error);
            if (error instanceof OfficeExtension.Error) {
                console.error('Office Extension é”™è¯¯:', error.debugInfo);
            }
        }
    }).catch(error => {
        console.error("Excelæ“ä½œå¤±è´¥:", error);
    });
}