export class Board {
    constructor(width = 10, height = 20) {
        this.width = width;
        this.height = height;
        this.grid = Array(height).fill().map(() => Array(width).fill(null));
    }

    isValidPosition(positions) {
        return positions.every(pos => 
            pos.x >= 0 && 
            pos.x < this.width && 
            pos.y < this.height && 
            (pos.y < 0 || !this.grid[pos.y][pos.x])
        );
    }

    placeBlock(block) {
        block.space.forEach(pos => {
            if (pos.y >= 0) {
                this.grid[pos.y][pos.x] = block.color;
            }
        });
    }

    clearLines() {
        let linesCleared = 0;
        // ä»åº•éƒ¨å‘ä¸Šæ£€æŸ¥ï¼Œè¿™æ ·åˆ é™¤è¡Œæ—¶ä¸ä¼šå½±å“æœªæ£€æŸ¥çš„è¡Œ
        for (let y = this.height - 1; y >= 0; y--) {
            // æ£€æŸ¥å½“å‰è¡Œæ˜¯å¦å·²æ»¡
            if (this.grid[y].every(cell => cell !== null)) {
                // åˆ é™¤æ»¡è¡Œå¹¶åœ¨é¡¶éƒ¨æ·»åŠ ç©ºè¡Œ
                this.grid.splice(y, 1);
                this.grid.unshift(Array(this.width).fill(null));
                linesCleared++;
                // y++ ç¡®ä¿é‡æ–°æ£€æŸ¥åŒä¸€ä½ç½®ï¼ˆå› ä¸ºåˆ é™¤åç´¢å¼•ä¼šå˜åŒ–ï¼‰
                // è¿™æ ·å¯ä»¥æ¸…é™¤è¿ç»­çš„å¤šè¡Œ
                y++;
            }
        }
        return linesCleared;
    }
}